import { useState } from "react";
import { FaBolt } from "react-icons/fa";
import s from "../style/style.module.sass";

export function FallbackImg(    props) {
    const [error, setError] = useState(false);

    function onerror(ev)  {
        setError(true);
    }

    return(
        <div>
            <img src={error?props.src2:(props.src||props.src2)} onError={onerror} style={{filter:error?"brightness(50%)":""}}></img>
            {error && 
                <div style={{color: "red", width: "40px", height:"50px"}} className={s.centerAbsolute}>
                    <FaBolt   size="30px"></FaBolt>
                    <div>URL</div>
                </div>
            }
        </div>
    );
}

export function FallbackImgImg(    props) {
    const [error, setError] = useState(false);

    function onerror(ev)  {
        setError(true);
    }

    return(
        <img src={error?props.src2:(props.src||props.src2)} onError={onerror} style={{filter:error?"brightness(50%)":""}} {...props}></img>
    );
}